*{
    overflow: hidden;
   
}
.main{
   overflow-y: scroll;
}
.main::-webkit-scrollbar {
    display: none;
}

.midbox{
   
    background-image: linear-gradient(to right, rgb(0, 101, 148), rgb(0, 101, 148));
    height: 25rem;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.textbox{
    
    /* margin-right: 7rem; */
    background-color: rgba(240, 248, 255, 0);
    height: 11rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.h1text{
    display: flex;
    background-color: rgba(255, 0, 0, 0);
    font-size: 2.5rem;
    font-weight: 700;
    width: 30rem;
    max-width: 40rem;
    height: 100%;
    color: rgb(255, 255, 255);
}
.h0text{
    background-color: rgba(235, 37, 37, 0);
    font-size: 1rem;
    width: 30rem;
    max-width: 40rem;
    height: 30%;
    color: rgba(255, 255, 255, 0.781);
}
.locationbox{
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    height: 3.8rem;
    width: 9rem;
    padding: 12px;
    margin: 0.1rem 0; 
    border-right: 2px solid rgb(221, 221, 221);
    display: flex;
    align-items: center;
    justify-content: center;
    justify-items: center;
}
.locationList{
    outline: none;
}
.Searchbox{
    /* margin-right: 7rem; */
    display: flex;
    justify-content: center;
    height: 4rem;
    width: 100%;
    margin-top: 0.6rem;
    margin-bottom: 0.5rem;
    background-color: rgba(255, 255, 255, 0);
   
}
.Search{
   margin-right: 7rem;
   background-color: rgb(255, 255, 255);
   border: 0px solid rgba(58, 88, 122, 0.308);
   outline: none;
   color: rgb(49, 49, 49);
   margin: 0.1rem 0;
   padding: 0.9rem 0.75rem;
   width: 50%;
   
   

}
.iconbox1{
   border-top-right-radius: 50px;
   border-bottom-right-radius: 50px;
   border: 0px solid rgba(58, 88, 122, 0.308);
   padding-left: 10px;
   padding-top: 6px;
   margin: 0.1rem 0;
   width: 4rem;
   background-color: rgb(255, 255, 255);
    
}
.icongool{
    border-radius: 50px;
    background-color: rgb(0, 130, 190);
    height: 3.1rem;
    width: 3.1rem;
    padding: 20%;
}
.icongool:hover{
    border-radius: 50px;
    background-color: rgb(0, 157, 230);
    height: 3.1rem;
    width: 3.1rem;
    padding: 20%;
    box-shadow: rgb(68, 68, 68) 0px 1px 10px;
}
.iconin{
    background-color: transparent;
    
}
/* ========================center box================= */


.cardscrool{
    /* height: 32rem; */
    background-color: rgb(205, 239, 255);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

}
.card{
    background-color: rgb(255, 255, 255);
    box-shadow: rgb(165, 165, 165) 0px 1px 4px;
    margin: 86px;
    margin-right: 0px;
    width: 300px;
    height: 350px;
    display: flex;
    padding: 20px;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
}
.card:hover{
    box-shadow: rgb(0, 47, 150) 0px 1px 15px;
    width: 300px;
    height: 350px;
}
.card-imgbox{
    background-color: transparent;
    height: 50%;
    width: 70%;
    margin-bottom: 30px;
}
.textbox1{
    background-color: rgba(0, 0, 255, 0); 
    width: 100%;
    height: 40%;
    padding: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
   
}
.cardtext1{
    background-color: transparent;
    font-size: 1.2rem;
    font-weight: 600;
}
.cardtext2{
    background-color: transparent;
    font-size: 0.9rem;
    color: rgb(112, 112, 112);
}
.NWZbtn{
    box-shadow: rgba(0, 0, 0, 0.705) 0px 1px 5px;
    background-color: rgb(188, 188, 255);
    border-radius: 50px;
    margin: 5px;
    height: 42px;
    width: 42px;
}








 /* ====================== contect bar ====================== */
.contectbar{
    background-color: rgb(0, 101, 148);
    height: 5rem;
    width: 100%;
    display: flex;
    padding: 2rem;
    align-items: center;
}
.line1{
    color: aliceblue;
    background-color: transparent;
}